import { useAccount, useReadContract, useWriteContract, useBalance, useContractRead } from 'wagmi';
import { formatEther, parseEther } from 'viem';
import { CONTRACT_ADDRESSES, MINING_CONTROLLER_ABI, ERC20_ABI, APECHAIN_ID, MAIN_CONTRACT_ABI } from '../config/contracts';
import { useEffect, useState } from 'react';

interface PlayerFacility {
  power: number;
  level: number;
  miners: number;
  capacity: number;
  used: number;
  resources: number;
  spaces: number;
  totalMined: bigint;
  hashRate: number;
  miningRate: number;
  networkShare: number;
}

interface PlayerStats {
  totalMined: bigint;
  hashRate: number;
  miningRate: number;
  networkShare: number;
}

export interface GameState {
  // User state
  isConnected: boolean;
  address: string | undefined;
  
  // Resources
  apeBalance: string;
  bitBalance: string;
  spacesLeft: number;
  gigawattsAvailable: number;
  
  // Mining stats
  miningRate: string;
  hashRate: string;
  blocksUntilHalving: string;
  networkHashRatePercentage: string;
  totalNetworkHashRate: string;
  
  // Network stats
  totalMinedBit: string;
  burnedBit: string;
  rewardPerBlock: string;
  
  // Mining rewards
  minedBit: string;
  
  // Facility state
  hasFacility: boolean;
  facilityData?: PlayerFacility;
  stats: PlayerStats;
  
  // Actions
  purchaseFacility: (referrer?: string) => Promise<void>;
  getStarterMiner: (x: number, y: number) => Promise<void>;
  claimReward: () => Promise<void>;
  upgradeFacility: () => Promise<void>;
  
  // Loading states
  isPurchasingFacility: boolean;
  isGettingStarterMiner: boolean;
  isClaimingReward: boolean;
  isUpgradingFacility: boolean;

  // New fields
  isGameActive: boolean;
  isUserActive: boolean;

  totalReferrals: number;
  totalBigEarned: string;
  refetch: () => void;
}

export function useGameState(): GameState {
  const { address, isConnected } = useAccount();
  
  // State variables
  const [apeBalance, setApeBalance] = useState('0');
  const [bitBalance, setBitBalance] = useState('0');
  const [minedBit, setMinedBit] = useState('0');
  const [hasFacility, setHasFacility] = useState(false);
  const [facilityData, setFacilityData] = useState<PlayerFacility>();
  const [spacesLeft, setSpacesLeft] = useState(0);
  const [gigawattsAvailable, setGigawattsAvailable] = useState(0);
  const [miningRate, setMiningRate] = useState('0');
  const [hashRate, setHashRate] = useState('0');
  const [blocksUntilHalving, setBlocksUntilHalving] = useState('0');
  const [networkHashRatePercentage, setNetworkHashRatePercentage] = useState('0');
  const [totalNetworkHashRate, setTotalNetworkHashRate] = useState('0');
  const [isPurchasingFacility, setIsPurchasingFacility] = useState(false);
  const [isGettingStarterMiner, setIsGettingStarterMiner] = useState(false);
  const [isClaimingReward, setIsClaimingReward] = useState(false);
  const [isUpgradingFacility, setIsUpgradingFacility] = useState(false);
  const [totalReferrals, setTotalReferrals] = useState(0);
  const [totalBigEarned, setTotalBigEarned] = useState('0');

  // Contract reads
  const { data: gameActiveState } = useContractRead({
    address: CONTRACT_ADDRESSES.MINING_CONTROLLER,
    abi: MINING_CONTROLLER_ABI,
    functionName: 'isGameActive',
    query: {
      enabled: true,
    },
  });

  const { data: userActiveState } = useContractRead({
    address: CONTRACT_ADDRESSES.MINING_CONTROLLER,
    abi: MINING_CONTROLLER_ABI,
    functionName: 'isUserActive',
    args: [address as `0x${string}`],
    query: {
      enabled: !!address,
    },
  });

  // Get native APE balance
  const { data: apeBalanceData } = useBalance({
    address: address as `0x${string}`,
    chainId: APECHAIN_ID,
    query: {
      enabled: !!address,
    },
  });

  // Get BIT token balance
  const { data: bitBalanceData } = useContractRead({
    address: CONTRACT_ADDRESSES.BIT_TOKEN,
    abi: ERC20_ABI,
    functionName: 'balanceOf',
    args: [address as `0x${string}`],
    query: {
      enabled: !!address,
    },
  });

  // Get facility data
  const { data: facilityDataResult, refetch: refetchFacility } = useContractRead({
    address: CONTRACT_ADDRESSES.MAIN,
    abi: MAIN_CONTRACT_ABI,
    functionName: 'getPlayerFacility',
    args: [address as `0x${string}`],
    query: {
      enabled: !!address,
    },
  });

  // Check if player has initialized facility
  const { data: hasInitializedFacility, refetch: refetchInitialized } = useContractRead({
    address: CONTRACT_ADDRESSES.MAIN,
    abi: MAIN_CONTRACT_ABI,
    functionName: 'initializedStarterFacility',
    args: [address as `0x${string}`],
    query: {
      enabled: !!address,
    },
  });

  // Get referral info
  const { data: referralInfo } = useContractRead({
    address: CONTRACT_ADDRESSES.MAIN,
    abi: MAIN_CONTRACT_ABI,
    functionName: 'getUserReferralInfo',
    args: [address as `0x${string}`],
    query: {
      enabled: !!address,
    },
  });

  // Update state when data changes
  useEffect(() => {
    if (apeBalanceData) {
      setApeBalance(apeBalanceData.formatted);
    }
  }, [apeBalanceData]);

  useEffect(() => {
    if (bitBalanceData) {
      setBitBalance(formatEther(bitBalanceData as bigint));
    }
  }, [bitBalanceData]);

  useEffect(() => {
    if (hasInitializedFacility !== undefined) {
      setHasFacility(!!hasInitializedFacility);
    }
  }, [hasInitializedFacility]);

  useEffect(() => {
    if (facilityDataResult) {
      const [power, level, miners, capacity, used, resources, spaces] = facilityDataResult as unknown as number[];
      setFacilityData({
        power,
        level,
        miners,
        capacity,
        used,
        resources,
        spaces,
        totalMined: BigInt(0),
        hashRate: 0,
        miningRate: 0,
        networkShare: 0,
      });
    }
  }, [facilityDataResult]);

  // Contract write functions
  const { writeContract: writeMainContract } = useWriteContract();

  const purchaseFacility = async (referrer?: string) => {
    if (!isConnected || !address) return;
    
    try {
      setIsPurchasingFacility(true);
      
      const zeroAddress = '0x0000000000000000000000000000000000000000' as `0x${string}`;
      
      await writeMainContract({
        address: CONTRACT_ADDRESSES.MAIN,
        abi: MAIN_CONTRACT_ABI,
        functionName: 'purchaseInitialFacility',
        args: [referrer ? (referrer as `0x${string}`) : zeroAddress],
        value: parseEther('.103'), // Initial facility price + fees
      });
      
      // Refresh data after purchase
      refetchInitialized?.();
      refetchFacility?.();
      
    } catch (error) {
      console.error('Error purchasing facility:', error);
    } finally {
      setIsPurchasingFacility(false);
    }
  };
  
  const getStarterMiner = async (x: number, y: number) => {
    if (!isConnected || !address || !hasFacility) return;
    
    try {
      setIsGettingStarterMiner(true);
      
      await writeMainContract({
        address: CONTRACT_ADDRESSES.MAIN,
        abi: MAIN_CONTRACT_ABI,
        functionName: 'getFreeStarterMiner',
        args: [BigInt(x), BigInt(y)],
      });
      
      // Refresh facility data after getting starter miner
      refetchFacility?.();
      
    } catch (error) {
      console.error('Error getting starter miner:', error);
    } finally {
      setIsGettingStarterMiner(false);
    }
  };
  
  const claimReward = async () => {
    if (!isConnected || !address || !hasFacility) return;
    
    try {
      setIsClaimingReward(true);
      
      await writeMainContract({
        address: CONTRACT_ADDRESSES.MINING_CONTROLLER,
        abi: MINING_CONTROLLER_ABI,
        functionName: 'claimReward',
      });
      
      // Reset mined BIT after claiming
      setMinedBit('0');
      
    } catch (error) {
      console.error('Error claiming reward:', error);
    } finally {
      setIsClaimingReward(false);
    }
  };
  
  const upgradeFacility = async () => {
    if (!isConnected || !address || !hasFacility) return;
    
    try {
      setIsUpgradingFacility(true);
      
      await writeMainContract({
        address: CONTRACT_ADDRESSES.MINING_CONTROLLER,
        abi: MINING_CONTROLLER_ABI,
        functionName: 'upgradeFacility',
      });
      
      // Refresh facility data after upgrade
      
    } catch (error) {
      console.error('Error upgrading facility:', error);
    } finally {
      setIsUpgradingFacility(false);
    }
  };
  
  const facility: PlayerFacility = {
    power: facilityData?.power || 0,
    level: facilityData?.level || 0,
    miners: facilityData?.miners || 0,
    capacity: facilityData?.capacity || 0,
    used: facilityData?.used || 0,
    resources: facilityData?.resources || 0,
    spaces: facilityData?.spaces || 0,
    totalMined: facilityData?.totalMined || BigInt(0),
    hashRate: facilityData?.hashRate || 0,
    miningRate: facilityData?.miningRate || 0,
    networkShare: facilityData?.networkShare || 0,
  };

  const stats: PlayerStats = {
    totalMined: facilityData?.totalMined || BigInt(0),
    hashRate: facilityData?.hashRate || 0,
    miningRate: facilityData?.miningRate || 0,
    networkShare: facilityData?.networkShare || 0,
  };
  
  return {
    isConnected,
    address,
    apeBalance,
    bitBalance,
    spacesLeft,
    gigawattsAvailable,
    miningRate,
    hashRate,
    blocksUntilHalving,
    networkHashRatePercentage,
    totalNetworkHashRate,
    totalMinedBit: stats.totalMined.toString(),
    burnedBit: '1,238,626.5',
    rewardPerBlock: '2.5',
    minedBit,
    hasFacility,
    facilityData,
    stats,
    purchaseFacility,
    getStarterMiner,
    claimReward,
    upgradeFacility,
    isPurchasingFacility,
    isGettingStarterMiner,
    isClaimingReward,
    isUpgradingFacility,
    isGameActive: !!gameActiveState,
    isUserActive: !!userActiveState,
    totalReferrals: referralInfo ? Number(referralInfo[0]) : 0,
    totalBigEarned: referralInfo ? formatEther(referralInfo[1]) : '0.00',
    refetch: () => {
      refetchInitialized?.();
      refetchFacility?.();
    },
  };
}
